.HW7Class {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.SuperSelectClass {
    margin-top: 10px;
    -webkit-appearance: none;
    padding: 7px 40px 7px 12px;
    width: 100%;
    border: 1px solid #E8EAED;
    border-radius: 5px;
    background: white;
    box-shadow: 0 1px 3px -2px #9098A9;
    cursor: pointer;
    font-family: inherit;
    font-size: 16px;
    transition: all 150ms ease;
}

.SuperRadioClass {
    margin-top: 10px;
}